import Draggable from "react-draggable";

import { RingDisplay } from "../ringdisplay/ringdisplay.js";

import "./overview.css";

export const Overview = ({
	discourseList,
	rotateDiscourse,
	setSelectedMathemes,
	handleCloseOverview
}) => {
	return (
		<Draggable handle=".overview-title" bounds=".main-container">
			<div className="overview">
				<div className="overview-title">
					Overview (All Rings)
					<button className="text-button close-overview-button" onClick={handleCloseOverview}>
					[<b>&#x2613;</b>]
					</button>
				</div>
				<div className="overview-body">
				<RingDisplay
					displayValues={["S\u2082", "a", "$", "S\u2081"]}
					rotateDiscourse={rotateDiscourse}
					setSelectedMathemes={setSelectedMathemes}
					{...discourseList}
				/>
				<RingDisplay
					displayValues={["a", "S\u2082", "$", "S\u2081"]}
					rotateDiscourse={rotateDiscourse}
					setSelectedMathemes={setSelectedMathemes}
					{...discourseList}
				/>
				<RingDisplay
					displayValues={["S\u2082", "$", "a", "S\u2081"]}
					rotateDiscourse={rotateDiscourse}
					setSelectedMathemes={setSelectedMathemes}
					{...discourseList}
				/>
				<RingDisplay
					displayValues={["S\u2082", "a", "S\u2081", "$"]}
					rotateDiscourse={rotateDiscourse}
					setSelectedMathemes={setSelectedMathemes}
					{...discourseList}
				/>
				<RingDisplay
					displayValues={["S\u2081", "a", "$", "S\u2082"]}
					rotateDiscourse={rotateDiscourse}
					setSelectedMathemes={setSelectedMathemes}
					{...discourseList}
				/>
				<RingDisplay
					displayValues={["S\u2081", "$", "a", "S\u2082"]}
					rotateDiscourse={rotateDiscourse}
					setSelectedMathemes={setSelectedMathemes}
					{...discourseList}
				/>
				</div>
			</div>
		</Draggable>
	);
};
